import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, timer, of, from } from 'rxjs';
import { retryWhen, delayWhen, tap, catchError, take, map, concatAll } from 'rxjs/operators';
import { Registro } from '../../interfaces/registro.interface';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer/ngx';
import { URL_SERVER } from 'src/environments/environment';
import { InicioService } from '../Almacenamiento/inicio.service';
import { NavController } from '@ionic/angular';

@Injectable({
    providedIn: 'root'
})

export class UsuariosLoginService {


    constructor(
                private http: HttpClient,
                private transfer: FileTransfer, 
                private almacenamiento: InicioService,
                private navCtrl: NavController) { }

    private hanflerError(error: Observable<any>) {

        let intento = 0;

        return error.pipe(
            delayWhen(() => timer(5000)),
            map((errorHttp: HttpErrorResponse) => {

                intento++;
                
                console.log('Entramos a intentos');
                if (errorHttp instanceof ErrorEvent) {
                    if (intento === 3) {
                        throw new Error('Error de Red');
                    }
                } else if (errorHttp instanceof ProgressEvent) {
                    if (intento === 3) {
                        throw new Error('Error de Red');
                    }
                } else {

                    if ( errorHttp.status === 404) {
                           throw new Error(' al realizar la solicictud ');
                        } else if ( errorHttp.status === 401) {
                            this.almacenamiento.terminarPartida().then(() => {
                                this.navCtrl.navigateRoot('/home');
                            });
                            throw new Error('No tiene permisos para realizar la petición ');
                         } else {
                             console.log(errorHttp, 'Error de servidor');
                             throw new Error(' No se pudo realizar la petición, intentalo más tarde ');
                        }
                }
            }),
            take(3)
        );
}

    registrar(data): Observable<any> {
        return this.http.post(`${URL_SERVER}/usuarios`, data).pipe(
            map((res: any) => {
                if (res.exe) {
                    res.response.exe = res.exe;
                    res.response.token = res.token;
                    return res.response;
                } else {
                    return res;
                }
            }),
            retryWhen(error => this.hanflerError(error))
        )
    }


    usuario(usuario): Observable<any> {
        return this.http.post(`${URL_SERVER}/usuarios/unico`, {buscar: usuario}).pipe(
            retryWhen(error => this.hanflerError(error))
        )
    }

    iniciarPartida(usuario, dispositivoID): Observable<any> {
        const options = {
            headers: new HttpHeaders({
                'x-dispositivoID': dispositivoID
            })
        };
        return this.http.post(`${URL_SERVER}/login`, usuario, options ).pipe(
            map((res: any) => {
                if (res.exe) {
                    res.response.exe = res.exe;
                    res.response.token = res.token;
                    return res.response;
                } else {
                    return res.exe;
                }
            }),
            retryWhen(error => this.hanflerError(error))
        );
    }

    fotoPerfilUsuarioMoto( imagen, token, tipo ) {
        const options: FileUploadOptions  = {
            fileKey: 'archivo',
            httpMethod: 'PUT',
            headers: {
                'x-token': token
            },
            params: {
                tipo
            }
        }

        // 4912685137238652

        const fileTransfer: FileTransferObject = this.transfer.create();

        return from(fileTransfer.upload(imagen, `${URL_SERVER}/upload`, options))
        .pipe(
            catchError(error => {
                console.log(`No se pudo subir la imagen `);
                throw new Error(`No se pudo subir la imagen `);
            })
        );
    }


    fotoPortada( imagen, token, old ) {
        const options: FileUploadOptions  = {
            fileKey: 'archivo',
            httpMethod: 'PUT',
            headers: {
                'x-token': token
            },
            params: {
                old
            }
        }

        // 4912685137238652

        const fileTransfer: FileTransferObject = this.transfer.create();

        return from(fileTransfer.upload(imagen, `${URL_SERVER}/upload/portada/`, options))
        .pipe(
            catchError(error => {
                console.log(`No se pudo subir la imagen `);
                throw new Error(`No se pudo subir la imagen `);
            })
        );
    }

    viewImagen(token) {
        const options = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };
        return this.http.get(`${URL_SERVER}/ver`, options).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    buscarUsuario(termino: string, token: string) {
        const headerOption = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/usuarios/${termino}`, headerOption ).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    buscarPerfilUsuario(usuario, token) {
        const HeaderOption = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/usuarios/perfil/${usuario}`, HeaderOption).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    traerMisNotificaciones(token, desde) {
        const HeaderOption = {
            headers: new HttpHeaders({
                'x-token': token,
                'x-desde': desde.toString()
            })
        };

        return this.http.get(`${URL_SERVER}/notificaciones/misnotificaciones/`, HeaderOption).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    notiCantidad(token) {
        const HeaderOption = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/notificaciones/numero/`, HeaderOption).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    ActualizarNotificaciones(token, id) {
        const HeaderOption = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/notificaciones/misnotificaciones/cambiarestado/`, {id}, HeaderOption).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    listarPaises() {
        return this.http.get(`${URL_SERVER}/paises` ).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    listarCiudades(pais) {
        const headerOption = {
            headers: new HttpHeaders({
                'x-pais': pais
            })
        };

        return this.http.get(`${URL_SERVER}/ciudad`, headerOption ).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    CodigoComprobar(data) {
     
        return this.http.put(`${URL_SERVER}/codigos`, data ).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    EditarCodigo(data) {
     
        return this.http.put(`${URL_SERVER}/codigos/cambiar`, data ).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }



    negocios() {
     
        return this.http.get(`${URL_SERVER}/tiponegocio` ).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }
}
