import { Component, OnInit } from '@angular/core';
import { NavController, MenuController } from '@ionic/angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { UsuariosLoginService } from '../../providers/Api/UsuariosLogin.service';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { Registro } from '../../interfaces/registro.interface';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';
import { PushService } from '../../providers/PushService/PushService.service';
import { Router } from '@angular/router';
import { ConexionService } from '../../providers/Conexiones/Conexion.service';
import { EntradasServices } from '../../providers/cargas/Entradas.service';
import { RegistroService } from '../../providers/Registro/registro.service';
import { MapasService } from '../../providers/Api/Mapar.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  public forma: FormGroup;
  public sesion = true;
  usuarioID = '';
  dispositivoID: any;

  vista = 1;

  miCodigo = '';

  // usuarios
  usuarioValido = false;
  msgUsuario = '';
  okUsuario = false;

  paises= [];
  ciudades = [];
  ciudad = false;
  load = false;

  pais = '';
  miciudad = '';

  claveValida = false;
  msgClave = '';
  clave = '';
  confirClave = '';
  correo = '';
  tel = 0;
  nusuario = '';
  ncompleto = '';
  okClave = false;

  negocios = [];

  escogidos = [];

  constructor(
    private apiService: UsuariosLoginService,
    private inicioService: InicioService,
    private toastService: ToastServices,
    private router: Router,
    private navCtrl: NavController,
    private OSService: PushService,
    private menuCtrl: MenuController,
    private conexion: ConexionService,
    private entradas: EntradasServices,
    private almacenamiento: InicioService,
    private registroService: RegistroService,
    private apiMapas: MapasService
  ) {

    this.forma = new FormGroup({
      'usuario': new FormControl('', Validators.required),
      'clave'  : new FormControl('', Validators.required)
    });

    this.OSService.$usuarioID.subscribe((numero: any) => {
      this.dispositivoID = numero;
    })

  }

  ngOnInit() {
    this.OSService.$usuarioID.subscribe((usuarioID: any) => {
      this.usuarioID = usuarioID;
    })
  }

  ionViewWillEnter() {
   this.listarPaises();
   this.listarNegocios();
  }

  iniciar() {
   if (this.forma.value.usuario !== '' && this.forma.value.clave !== '') {
    this.sesion = false;
    this.apiService.iniciarPartida(this.forma.value, this.dispositivoID).subscribe(
    (resp: any) => {

      if (resp.exe) {
        this.sesion = true;
        this.inicioService.cambiarEstado(false);
        resp.usuarioID = this.usuarioID;
  
        this.inicioService.guardarUsuario(resp).then(() => {
          this.conexion.cargaLista();
          this.toastService.alertApplication('Bienvenido!!!', true).then(() => {
            this.router.navigate(['/central/menu']);
            this.entradas.crearEntrada();
          });
      }, (error) => {
          this.toastService.alertApplication(error.message, false);
      });

      } else {
        this.sesion = true;
        this.toastService.alertApplication('Credenciales Incorrectas', false);
      }

    },
    (error) => {
      this.sesion = true;
      this.inicioService.cambiarEstado(true);
      this.toastService.alertApplication(error.message, false);
    })
   }
  }

  ir() {
    this.vista = 2;
  }

  retroceder() {
    this.vista = 1;
  }
  

  codigo() {
    this.vista = 3;
  }

  verificar() {

    if (this.miCodigo !== '') {
      this.apiService.CodigoComprobar({codigo: this.miCodigo}).subscribe((data: any) => {
        if (data) {
          if ( data.response.length === 0) {
            this.toastService.toastError('Ingrese un código válido');
          } else {
            this.vista = 4;
            this.toastService.toastNotificacion('Complete toda la información');
          }
         
        } else {
          this.toastService.toastError('No se pudo comprobar el código, inténtelo más tarde');
        }
      }, (err) => {
          this.toastService.toastError('No se pudo comprobar el código, inténtelo más tarde')
      })
    } else {
      this.toastService.toastError('Ingrese un código válido');

    }

  }


  comprobarUsuario(event) {
    if (event.detail.value.length > 5) {
      this.usuarioValido = true;
      this.apiService.usuario(event.detail.value).subscribe(async (data) => {
        console.log(data);
        if (data.exe) {
          this.okUsuario = false;
          this.msgUsuario = 'El usuario ya se encuentra en uso';
        } else {
          this.okUsuario = true;
          this.msgUsuario = 'El nombre de usuario esta disponible';
          this.nusuario = event.detail.value;
        }
      }, async (error) => {
         this.toastService.toastError('No se pudo comprobar el usuario, inténtelo más tarde');
      })
    } else {
      this.usuarioValido = false;
    }
  }



  listarNegocios() {
    this.apiService.negocios().subscribe((data: any) => {
      if (data.exe) {
        this.negocios = data.response;
      } else {
        this.negocios.push('Taller Mécanico');
      }
    }, (err) => {
      this.negocios.push('Taller Mécanico');
    })
  }

  
  listarPaises() {
    this.apiService.listarPaises().subscribe((data: any) => {
      if (data.exe) {
        this.paises = data.response;
      } else {
        this.paises.push('Colombia');
      }
    }, (err) => {
      this.paises.push('Colombia');
    })
  }


  listarCiudades( event ) {
    console.log(event.detail)
    this.pais = event.detail.value.nombre;
    this.miciudad = '';
    this.apiService.listarCiudades(event.detail.value._id).subscribe((data: any) => {
     if (data.exe) {
       this.ciudades = data.response;
       this.ciudad= true;
     } else {
       this.toastService.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
     }
   }, (err) => {
     this.toastService.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
   })
 }

 seleccionarCiudad( event ) {
   this.miciudad = event.detail.value;
 }

 comprobarClave( event, tipo ) {
  this.claveValida = true;
  if (event.detail.value.length >= 8) {
    if (tipo === 1) {
      this.clave = event.detail.value;
    } else {
      this.confirClave = event.detail.value;
    }
    if (this.clave === this.confirClave) {
      this.msgClave = 'Las claves coínciden correctamente';
      this.okClave = true;
    } else {
      this.msgClave = 'Las contraseñas no coínciden';
      this.okClave = false;
    }
  } else {
    this.okClave = false;
    this.msgClave = 'Debe tener como mínmo 8 caracteres';
  }
 }

 agregarNegocios( event ) {
   if (event.detail.checked) {
    if (!this.escogidos.find((item) => item === event.detail.value)) {
      this.escogidos.push(event.detail.value);
    }
   } else {
     this.escogidos = this.escogidos.filter((item) => item !== event.detail.value);
   }

   console.log(this.escogidos);
    
 }

 guardar() {
   if (this.okUsuario && this.okClave && this.pais !== '' && this.miciudad !== '' && this.ncompleto !== '') {
    this.apiService.registrar({
      nombreCompleto: this.ncompleto,
      nombreUsuario: this.nusuario,
      clave: this.clave,
      correo: this.correo,
      tel: this.tel,
      role: 'TALLER_ROLE',
      pais: this.pais,
      ciudad: this.miciudad,
      tipoNegocio: this.escogidos,
      usuarioID: this.usuarioID
  }).subscribe(
      (datos) => {
        console.log(datos, 'data', {
          nombreCompleto: this.ncompleto,
          nombreUsuario: this.nusuario,
          clave: this.clave,
          correo: this.correo,
          tel: this.tel,
          role: 'TALLER_ROLE',
          pais: this.pais,
          ciudad: this.miciudad,
          tipoNegocio: this.escogidos,
          usuarioID: this.usuarioID
      });
        
        this.almacenamiento.guardarUsuario(datos).then(() => {
          this.toastService.toastExitoso('Usuario registrado corectamente');

          this.registroService.cambioEstado(true);
          this.almacenamiento.cambiarEstado(false);
          this.navCtrl.navigateRoot(['/central']);
          this.apiService.EditarCodigo({codigo: this.miCodigo}).subscribe((data: any) => {
            console.log('Código cambiado')
          })
          // tslint:disable-next-line:max-line-length
          this.apiMapas.crearSede(datos.token, {ubicacion: {lat: 0, lng: 0}, nombre: 'SEDE ' + this.ncompleto, tipo: this.escogidos, pais: this.pais, ciudad: this.miciudad}).subscribe((data: any) => {
            if (!data ){
              this.toastService.toastError('No se pudo crear la sede');
            }
          }, (err) => {
            this.toastService.toastError('No se pudo crear la sede');
          })

        });

        }, (error) => { 
        this.toastService.toastError('No se pudo registrar el usuario, inténtelo más tarde');
       });
   } else {
     this.toastService.toastError('Existen campos sin diligenciar correctamente');
   }
 }


}
